

















import AsModal from "@/mixins/AsModal";
import {Component, Emit, Mixins} from "vue-property-decorator";

@Component
export default class UploadAudioModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(): void {}

  clean(): void {
    this.dismiss();
  }
  audio_url: string = "";
  upload(options: {file: File}): Promise<void> {
    return this.api
      .upload(options.file, "path")
      .then((res) => {
        this.audio_url = res;
      })
      .catch(console.error);
  }
}
